* {
  box-sizing: border-box;
  -webkit-user-select: none;
  user-select: none;
  margin: 0;
  padding: 0;
}

body {
  color: #fff;
  padding: 40px;
  font-family: Helvatica Neue, sans-serif;
}

.calculator {
  background-color: #000;
  border-radius: 50px;
  width: 500px;
  padding: 20px;
}

.secondary-display {
  text-align: right;
  height: 60px;
  font-size: 60px;
}

.primary-display {
  text-align: right;
  height: 120px;
  font-size: 120px;
}

.buttons-container {
  grid-gap: 20px;
  grid-template-rows: repeat(5, 100px);
  grid-template-columns: repeat(4, 100px);
  margin-top: 40px;
  display: grid;
}

.button {
  background-color: #333;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 100px;
  height: 100px;
  font-size: 42px;
  display: flex;
}

.function {
  color: #000;
  background-color: #999;
}

.operator {
  color: #000;
  background-color: orange;
}

.operator-active {
  background-color: #fff;
}

.button.zero {
  border-radius: 55px;
  grid-column: 1 / 3;
  width: 220px;
}
